import { CopyReceiver } from './CopyReceiver'

export const Pairs = ({ pairs, showGroups }) => {
  if (pairs.length < 1) {
    return null
  }

  return (
    <table>
      <thead>
        <tr>
          <th colSpan={showGroups ? 2 : 1}>–¢–∞–π–Ω—ã–π —Å–∞–Ω—Ç–∞</th>
          <th colSpan={showGroups ? 2 : 1}>–ü–æ–ª—É—á–∞—Ç–µ–ª—å –ø–æ–¥–∞—Ä–∫–∞</th>
        </tr>
        {showGroups && (
          <tr>
            <th>–ò–º—è</th>
            <th>–ì—Ä—É–ø–ø–∞</th>
            <th>–ò–º—è</th>
            <th>–ì—Ä—É–ø–ø–∞</th>
          </tr>
        )}
      </thead>
      <tbody>
        {pairs.map(([a, b]) => (
          <tr key={a.name}>
            <td>{a.name}</td>
            {showGroups && <td>{a.group}</td>}
            <td>
              <CopyReceiver giver={a.name} receiver={b.name} />
            </td>
            {showGroups && <td>{b.group}</td>}
          </tr>
        ))}
      </tbody>
    </table>
  )
}
