import { useLayoutEffect, useState } from 'react'

import { Pairs } from './Pairs'
import matchPairs from './match-pairs'

import './matches.css'

const errorMessage =
  '–ù–µ–≤–æ–∑–º–æ–∂–Ω–æ —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ —Å —É–∫–∞–∑–∞–Ω–Ω—ã–º–∏ –ª—é–¥—å–º–∏, –∏–ª–∏ –ø—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑ –∏–ª–∏ –∏–∑–º–µ–Ω–∏—Ç–µ —Å–≤–æ—é –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—é, —á—Ç–æ–±—ã –ø–æ–∑–≤–æ–ª–∏—Ç—å –∫–∞–∂–¥–æ–º—É —Å–æ–≤–ø–∞–¥–∞—Ç—å —Å –∫–µ–º-—Ç–æ.'

export const Matches = ({ person }) => {
  const [pairs, setPairs] = useState([])
  const [error, setError] = useState(null)
  const [showGroups, setShowGroups] = useState(false)

  useLayoutEffect(() => {
    setPairs([])
  }, [person])

  const calculateMatches = async () => {
    setError(null)
    try {
      setPairs(matchPairs(person))
    } catch (e) {
      setError(new Error(errorMessage))
      setPairs([])
    }
  }

  return (
    <>
      <button
        onClick={calculateMatches}
        type="button"
        disabled={person.length < 3}
      >
        –°–æ–≤–ø–∞–¥–µ–Ω–∏—è
      </button>
      <label
        style={{
          paddingLeft: '15px',
        }}
      >
        <input
          type="checkbox"
          onChange={() => setShowGroups((prevState) => !prevState)}
          checked={showGroups}
        />
        –ü–æ–∫–∞–∑—ã–≤–∞—Ç—å –≥—Ä—É–ø–ø—ã
      </label>
      {error && <p className="error">{error.message}</p>}
      <Pairs pairs={error ? [] : pairs} showGroups={showGroups} />
    </>
  )
}
