import { useForm } from 'react-hook-form'
import { ErrorMessage } from '@hookform/error-message'

import { ValidationError } from '../../common/ValidationError'

import '../../common/css/form.css'

export const PersonForm = ({ usedNames, usedGroups, onSubmit }) => {
  const { errors, handleSubmit, register, reset } = useForm()

  const submitHandler = (data) => {
    onSubmit({
      name: data['person-name'],
      group: data['person-group'] === '' ? undefined : data['person-group'],
    })
    reset()
  }

  return (
    <form onSubmit={handleSubmit(submitHandler)}>
      <fieldset>
        <legend>–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤–æ–≥–æ —á–µ–ª–æ–≤–µ–∫–∞</legend>
        <label htmlFor="person-name" className="label-required">
          –ò–º—è
        </label>
        <input
          id="person-name"
          name="person-name"
          type="text"
          aria-required
          ref={register({
            required: '–¢—Ä–µ–±—É–µ—Ç—Å—è –∏–º—è',
            validate: (value) =>
              (value && !usedNames.includes(value)) ||
              '–ò–º—è –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å —É–Ω–∏–∫–∞–ª—å–Ω—ã–º',
          })}
        />
        <ErrorMessage errors={errors} name="person-name" as={ValidationError} />

        <label htmlFor="person-group">–ì—Ä—É–ø–ø–∞</label>
        <input
          id="person-group"
          name="person-group"
          type="text"
          list="groupOptions"
          autoComplete="off"
          ref={register}
        />
        <datalist id="groupOptions">
          {usedGroups.map((group) => (
            <option key={group}>{group}</option>
          ))}
        </datalist>
        <p className="meta">
          –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ –≥—Ä—É–ø–ø—ã –¥–ª—è –ø—Ä–æ—Å—Ç—ã—Ö –∏—Å–∫–ª—é—á–µ–Ω–∏–π, –ª—é–¥–∏ –≤ –æ–¥–Ω–æ–π –≥—Ä—É–ø–ø–µ –Ω–µ
          –ø–æ–ø–∞–¥–∞—é—Ç –¥—Ä—É–≥ –¥—Ä—É–≥—É.
        </p>
        <button type="submit">–î–æ–±–∞–≤–∏—Ç—å —á–µ–ª–æ–≤–µ–∫–∞</button>
      </fieldset>
    </form>
  )
}
